export default {
	data() {
		return {
			markActive: 'none',
			move:'',
		};
	},
	methods: {
		back() {
			uni.navigateBack({});
		},
		show() {
			this.markActive = 'markActive';
			this.move='move'
		},
		hidden() {
			this.markActive = 'hideActive';
			this.move='stop'
			setTimeout(() => {
				this.markActive = 'none';
			}, 300);
		}
	}
};
